import Config from "./contracts/config.json";

export default class config {
	static _network = undefined;
	static version() {
		return Config.version;
	}

	static network() {
		return config._network || Config.network;
	}

	static setNetwork(_network) {
		return (config._network = _network);
	}

	static address() {
		return Config.contract[config.network()];
	}

	static codeUrl() {
		const contractUrl =
			Config.network !== "test"
				? `https://etherscan.io/address/${config.address()}#code`
				: `https://ropsten.etherscan.io/address/${config.address()}#code`;
		return contractUrl;
	}

	static getGameAddress(name, address) {
		name = (name + "").trim();
		const games = Config.games;
		for (var i = 0; i < games.length; i++) {
			// console.log("games[i]" );
			// console.log(games[i]);
			// console.log("games[i].name:" + games[i].name);
			const gname = games[i].name.trim();
			if (
				gname == name ||
				gname + "/" == name ||
				"/" + gname + "/" == name ||
				games[i].address == address
			) {
				//console.log("games[i].address:" + games[i].address);
				return games[i].address;
			}
		}
		return undefined;
	}

	static gameUrl(name, address) {
		const addr = config.getGameAddress(name, address);
		//console.log(addr);
		if (addr) {
			return "/" + name + "/";
		}
		return "/game?n=" + name; //Config.network !== "test" ? "/" + name : "/game?n=" + name;
	}

	static gameNames() {
		const games = Config.games;
		const names = [];
		const filters = [
			"index",
			"game",
			"games",
			"about",
			"howtoplay",
			"account"
		];
		for (var i = 0; i < games.length; i++) {
			if (filters.indexOf(games[i].name) < 0) {
				names.push(games[i].name);
			}
		}

		return names;
	}

	static apikey() {
		return "XRPRF88GE7HWHHQ68CSC8R6WB9IQUS8UEQ";
	}

	static async usdrate() {
		try{
			var rateRequest = fetch(
				"https://api.coinmarketcap.com/v1/ticker/ethereum/"
			).then(function (response) {
				return response.json();
			});

			const ratejson = await rateRequest;
			const rate = ratejson[0].price_usd;
			console.log("rate:" + rate);
			return rate;
		}catch(e){
			console.log(e);
		}
		return 202.53;
	}

	static GACode(){
		return Config["GACode"];
	}
}
